.container {
    width: 100;
    height: 92vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background:hsl(210, 38%, 95%);
}

.form{
  background:#fff;
  padding:30px 34px;
  margin:24px 0 0;
  min-width:400px;
  min-height:273px;
  border-radius:5px;
  line-height: 24px;
  box-shadow:3px 1px 8px rgba(255,255,255,.5),3px 2px 8px rgba(0,0,0,.1)
}
.input-field{
  display:flex;
  flex-direction:column;
  grid-gap:5px;
  gap:5px;
  width:100%;
  margin-bottom:15px;
}
.error{
  border: 1px solid red;
}
input[type="text"],input[type="password"]{
  padding:15px;
  border-radius:5px;
  border:1px solid transparent;
  background:hsl(210, 38%, 95%);
  line-height: 24px;
  outline:none;
}
input[type="email"]:focus,input[type="password"]:focus{
  border-bottom:1px solid orange;
/*   border-radius:5px; */
}
input[type="checkbox"]{
  margin-right:10px;
}
.action {
  align-items:center;
  border-color: hsl(214, 32%, 91%);
  color: hsl(220, 26%, 14%);
  display:flex;
  grid-gap: normal;
  gap: normal;
  justify-content: flex-end;
  line-height: 24px;
  margin: 16px 0px 0px;
}
.action a{
  border-color:hsl(214, 32%, 91%);
  color:hsl(220, 26%, 14%);
  font-family:Nunito;
  font-size:14px;
  grid-gap:normal;
  gap:normal;
  line-height:21px;
  text-transform:capitalize;
}
.btn{
  padding:10px 20px;
  align-items: flex-start;
  background-color: hsl(218, 17%, 35%);
  border-color: hsla(0, 0%, 0%, 0);
  border-color: hsla(0, 0%, 0%, 0);
  border-radius: 4px;
  border-style: solid;
  border-width: 1px;
  box-shadow: hsla(0, 0%, 0%, 0.1) 0px 4px 6px -1px;
 color: hsl(0, 0%, 100%);
  font-size:14px;
  font-weight:600;
  grid-gap: normal;
  gap: normal;
  letter-spacing: 1.4px;
  line-height: 21px;
  margin: 0px 0px 0px 16px;
  padding: 8px 16px;
  text-align: center;
  text-transform: uppercase;
  color:hsl(0, 0%, 100%);
  font-family:Nunito;
  cursor:pointer;
  transition:.2s ease-in-out;
}
.btn:hover{
  background:hsl(218, 17%, 24%);
}
a{
  text-decoration:none;
}
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

